.cardItem {
  .item {
      .lg-block({
          justify-content: center;
      });
    display: flex;
    margin-bottom: 20px;
    flex-wrap: wrap;
    .cardImg {
        margin-right: 25px;
    }
    .cardTitle {
        width: 30%;.lg(width,100% );
      .name {
        font-size: 25px;
        font-family: "Roboto Slab";
        color: rgb(133, 133, 133);
        margin-bottom: 20px;
      }
      .price {
        margin-bottom: 20px;
        font-size: 30px;
        font-family: "Roboto Slab";
        color: rgb(0, 160, 255);
        font-weight: bold;
      }
      .star {
        margin-bottom: 30px;
        i {
          color: #ffc000;
          &:nth-child(5){
              color: #858585;
          }
        }
      }
      .info {
        margin-bottom: 50px;
        font-size: 16px;
        font-family: "Roboto Slab";
        color: rgb(133, 133, 133);
      }
      button {
        background-color: rgb(0, 160, 255);
        width: 220px;
        height: 50px;
        font-size: 16px;
        font-family: "Roboto Slab";
        color: rgb(255, 255, 255);
        font-weight: bold;
      }
    }
  }
}