.help{
    padding: 50px 40px;
    .sm(padding, 50px 0); 
    background: url('../img/indexImg/blue.png');
    .title {
      
        h3 {
            .lg(text-align, center);
          font-size: 25px;
          font-family: "Roboto Slab";
          color: #fff;
          font-weight: bold;
          margin-bottom: 30px;
        }
      }
    .content{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
       
        .nav{
           width: 50%; .lg(width, 100%); 
           padding: 0;
          .lg-block({
             flex-direction: column;text-align: center; 
          });
           li{
               a{
                    font-size: 16px;
                    font-family: "Roboto Slab";
                    color: rgb(255, 255, 255);
                    &:hover{
                        color: #004771;
                    }
               }
           }
        }
    }
}