header {
  padding-bottom: 30px;
 .menu-top {
   background: #fff;
   .navbar{
     .navbar-toggler{
       border: none;
      i{
        color: #00a0ff;
        font-size: 28px;
        font-weight: 900;
      } 
     }
    .navbar-brand{
      font-size: 18px;
      font-family: "Roboto Slab";
      color: rgb(0, 160, 255);
      font-weight: bold;
    }
     .navbar-nav{
       li{
         list-style: none;
        
          a{
         
            text-transform: capitalize;
              font-size: 16px;
              font-family: "Roboto Slab";
              color: rgb(132, 132, 132);
              padding:10px 20px;
              .sm(padding, 10px);
         }
      
       }
     }
      .shop-card{
           display: flex;
           .quarity{
             align-self: center;
             margin-right: 8px;
             width: 25px;
             height: 25px;
             border-radius: 50%;
             background-color: #ffc600;
             text-align: center;
             font-size: 12px;
             line-height: 2;
             color: #fff;
           }
           .bag{
             p{
               margin: 0;
               color: #ffc600;
               font-size: 16px;
               text-transform: capitalize;
             }
             .bank{
               color: #858585;
               font-size: 16px;
             }
           }
         }
   }
 }
 .sina-nav{
 
   .extension-nav{ 
      width: 100%;
     ul{
       width: 100%;
       display: flex;
       justify-content: space-between;
       .dropdown{
         a{
           color: #00a0ff !important;
           font-weight: 400;
           font-size: 16px;
           text-transform: capitalize;
         }
        .dropdown-menu{
          left: auto !important;
          right: 0;
          display: flex;
          flex-direction: column;
        }
       }
       .widget-bar-btn{
         a{
           i{
             color: #00a0ff;
             font-size: 28px;
             font-weight: 900;
           }
         }
       }
     }
   }
 }
form{
  position: relative;
  display: flex;
  height: 60px;.lg(height,45px);
  .search{
    width: 100%;
   height: inherit;
   border: none;
   padding-left: 3%;
   &::placeholder{
      font-size: 16px;
      font-family: "Roboto Slab";
      color: rgb(132, 132, 132);
   }
  }
  .submit{
    position: absolute;
    z-index: 5;
    right: 0;
      background-color: rgb(0, 160, 255);
   height: inherit;
  width: 85px;
      font-size: 16px;
      font-family: "Roboto Slab";
      color: rgb(255, 255, 255);
   border: none;
  }
}
}



