footer{
    padding-top: 40px;
    .container {
        padding: 0;
        .sm(padding,0 15px);
        .title {
        h2 {
          font-size: 25px;
          font-family: "Roboto Slab";
          color: rgb(0, 160, 255);
          font-weight: bold;
          margin-bottom: 40px;
        }
      }
      ul{
          padding: 0;
          margin-bottom: 30px;
          li{
              list-style: none;
              margin-bottom: 10px;
              a{
                  text-decoration: none;
                  font-size: 16px;
                  font-family: "Roboto Slab";
                  color: rgb(133, 133, 133);
                  font-weight: 400;
                
                &:hover{
                    color: #007bff;
                }
              }
          }
      }
    }
    
}