.faq{
    padding-top: 80px;
  .container{
      padding: 0;  .sm(padding,0 15px);
      .title {
        h2 {
          font-size: 25px;
          font-family: "Roboto Slab";
          color: rgb(0, 160, 255);
          font-weight: bold;
          margin-bottom: 80px;
        }
      }
      .faqtitle{
            font-size: 18px;
            font-family: "Roboto Slab";
            color: rgb(0, 160, 255);
            font-weight: bold;
          margin-bottom: 25px;
          display: block;
          text-decoration: none;
      }
      ul{
         
          padding: 20px 0;
          display: flex;
          .sm-block({
              flex-direction: column;
          });
          li{
              flex-direction: column;
              display: flex;
              list-style: none;
              margin: 10px 0;
              .sm-block({
               
                margin: 10px 0;
            });
              &:nth-child(2){
                  margin-left: 20px;
                  .sm-block({
               
                    margin-left: 0;
                });
              }
              .faqText{
                    font-size: 16px;
                    font-family: "Roboto Slab";
                    color: rgb(133, 133, 133);
                    font-weight: 400;
                  &:hover{
                      color: #007bff;
                  }
              }
          }
      }
  }  
}