.blogPost {
  .post {
    display: flex;
    padding: 30px 0;
    justify-content: center;
    flex-wrap: wrap;
    img {
      margin-right: 40px;
      .md(margin-right, 0);
    }
    .text {
      max-width: 530px;
      padding-top: 20px;
      .md(margin-left, 0);
      margin-left: 40px;
      h3 {
        font-size: 20px;
        font-family: "Roboto Slab";
        color: rgb(255, 255, 255);
        font-weight: bold;
        max-width: 512px;
      }
      p {
        font-size: 16px;
        font-family: "Roboto Slab";
        color: rgb(255, 255, 255);
        max-width: 530px;
      }
      button {
        background-color: rgb(255, 255, 255);
        max-width: 250px;
        height: 45px;
        font-size: 16px;
        font-family: "Myriad Pro";
        color: rgb(132, 132, 132);
        border: none;
        .lg-block( {
          display: block;
          margin: auto;
        }
        );
      }
    }
    &.green {
      background: url('../img/indexImg/green.png');
    }
    &.yellow {
      background: url('../img/indexImg/yellow.png');
    }
    &.blue {
      background: url('../img/indexImg/blue.png');
    }
  }
}