.yellowSearch{
    padding-top: 20px;
    padding-bottom: 65px;
    background: url('../img/indexImg/yellow.png');
    h3{
            font-size: 30px;
            font-family: "Roboto Slab";
            color: rgb(255, 255, 255);
            text-align: center;
          
    }
    h5{
     margin-bottom: 25px;
            font-size: 16px;
            font-family: "Roboto Slab";
            color: rgb(255, 255, 255);
            text-align: center;
          
    }
    form{
   justify-content: center;
   .sm-block({
      align-items: center;
      flex-direction: column; 
   });
        flex-wrap: wrap;
        display: flex;
        .yellowText{
            margin: 15px;
            padding-left: 25px;
            &::placeholder{
                    font-size: 16px;
                    font-family: "Roboto Slab";
                    color: rgb(255, 192, 0);
                  
            }
         border: none;
                background-color: rgb(255, 255, 255);
                max-width: 350px;
                height: 43px;
              
        }
        .yellowSubmit{
          margin: 15px;
                background-color: rgb(132, 132, 132);
                max-width: 350px;
                height: 43px;
             border: none;
                    font-size: 16px;
                    font-family: "Roboto Slab";
                    color: rgb(255, 255, 255);
                    &:hover{
                        background: #00a0ff;
                    }
                  
        }
    }
}