.products {
  padding-top: 80px;
  .title {
    h2 {
      font-size: 25px;
      font-family: "Roboto Slab";
      color: rgb(0, 160, 255);
      font-weight: bold;
      margin-bottom: 80px;
    }
  }
  .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .card {
      position: relative;
      max-width: 289px;
    
      &:hover{
        .card-price {
            opacity: 0;
            transition: 1.5s;
        }
        .card-name{
            background: rgb(0, 160, 255);
            color: #fff;
            transition: 1.5s;
        }
      }
    
      .card-price {
        border: 1px solid rgb(255, 255, 255);
        border-bottom: none;
        width: 100%;
        top: 0;
        font-size: 14px;
        font-family: "Roboto Slab";
        color: rgb(255, 255, 255);
        text-align: center;
        position: absolute;
        background-color: rgb(133, 133, 133);
        height: 25px;
      }
      img {}
      .card-name {
     padding-left: 15px;
          padding-top: 10px;
          padding-bottom: 10px;
        border: 1px solid rgb(255, 255, 255);
        border-top: none;
        width: 100%;
        bottom: 0;
        font-size: 14px;
        font-family: "Roboto Slab";
        color: rgb(255, 255, 255);
        position: absolute;
        background-color: rgb(133, 133, 133);
      }
    }
  }
  button {
      display: block;
      margin: auto;
    max-width:1154px;
    background-color: rgb(0, 160, 255);
    height: 50px;
    font-size: 16px;
    font-family: "Roboto Slab";
    color: rgb(255, 255, 255);
    font-weight: bold;
    text-align: center;
    border: none;
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
  }
}