.card-info {
  .nav {
    .sm(justify-content, center);
    li {
      padding: 20px 30px;
      .lg-block( {
        padding: 20px 15px;
      }
      );
      &.active2 {
        background: #f7f6f6;
        a {
          color: #00a0ff;
        }
      }
      a {
        text-decoration: none;
        font-size: 20px;
        font-family: "Roboto Slab";
        color: rgb(133, 133, 133);
        font-weight: bold;
        .lg-block( {
          font-size: 16px;
        }
        );
      }
    }
  }
  .tab-content {
    background: #f7f6f6;
    p {
      font-size: 16px;
      color: rgb(133, 133, 133);
      max-width: 831px;
      font-family: 'Roboto Slab';
      padding: 10px;
    }
  }
}