.clients {
  position: relative;
  .title {
    text-align: center;
    h3 {
      font-size: 25px;
      font-family: "Roboto Slab";
      color: rgb(0, 160, 255);
      font-weight: bold;
      margin-bottom: 80px;
    }
  }
  .item {
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .title {
      padding-left: 5%;
      padding-top: 5%;
      top: 0;
      position: absolute;
      background: url('../img/indexImg/carouseltitle.png');
      width: 100%;
      height: 100%;
      p {
        text-align: left;
        font-size: 20px;
        .sm(font-size,
        14px);
        font-family: "Roboto Slab";
        color: rgb(255, 255, 255);
      }
    }
  }
}