.intro {
  background: #f7f6f6;
 
  .carousel {
    .carousel-indicators {
      li {
        width: 8px;
        height: 8px;
        background-color: #fff;
        border-radius: 50%;
        &.active {
          background: #00a0ff;
        }
      }
    }
    .carousel-inner {
      .carousel-item {
        .title {
          position: absolute;
          z-index: 5;
          background: url('../img/indexImg/carouseltitle.png');
          width: 100%;
          height: 100%;
          padding-top: 5%;
          padding-left: 5%;
          h3 {
            font-size: 25px;.sm(font-size, 16px);
            font-family: "Roboto Slab";
            color: rgb(255, 255, 255);
            font-weight: bold;
          }
        }
        img {
          width: 100%;
          object-fit: cover;
        }
      }
    }
  }
  .header-bottom {
    padding-bottom: 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    button {
      background-color: rgb(255, 255, 255);
      width: 34%;
      .md(width,
      50%);
      .sm(width,
      100%);
      height: 100px;
      font-size: 30px;
      font-family: "Roboto Slab";
      color: rgb(132, 132, 132);
      font-weight: bold;
      border: none;
      &:hover {
        background: #00a0ff;
        color: #fff;
      }
    }
    .app {
      width: 165px;
      align-self: center;
      .md(margin,
      auto);
      a {
        text-decoration: none;
        display: flex;
        font-size: 18px;
        font-family: "Roboto Slab";
        color: rgb(132, 132, 132);
        text-transform: capitalize;
        img {
          margin-right: 5px;
          display: block;
        }
      }
    }
  }
}